.product_grid {
  list-style-type: none;
  margin: 0;
  padding: 0;
  @media screen and (min-width: 1200px) {
    grid-template-columns: repeat(5, minmax(0, 1fr));
  }
  @media screen and (min-width: 1700px) {
    grid-template-columns: repeat(6, minmax(0, 1fr));
  }
}
.product_card {
  position: relative;
  .flex {
    gap: 8px;
    .text {
      font-family: var(--font-heading-family);
      text-align: center;
      order: 2;
      flex: 1 0 100%;
      .price {
        margin-top: 4px;
        dl {
          flex: 1 0 100%;
        }
        text-align: center;
        &.price--sold-out {
          opacity: 0.5;
        }
      }
      .soldout {
        margin-top: 0.5;
        opacity: 0.5;
      }
      h3.h5 {
        font-family: var(--font-heading-family);
        margin: 0;
        color: var(--color-base-text);
        font-size: 100%;
        a {
          color: var(--color-base-text);
        }
      }
    }
    .image {
      order: 1;
      flex: 1 0 100%;
    }
  }
}

